e: 24px;\n  display: none;\n  transition: all 0.4s;\n  outline: none;\n  cursor: pointer;\n}\n  \n  #mobile-nav-toggle i {\n  color: #fff;\n}\n  \n  /* Mobile Nav Styling */\n  \n  #mobile-nav {\n  position: fixed;\n  top: 0;\n  padding-top: 18px;\n  bottom: 0;\n  z-index: 998;\n  background: rgba(6, 12, 34, 0.9);\n  left: -260px;\n  width: 260px;\n  overflow-y: auto;\n  transition: 0.4s;\n}\n  \n  #mobile-nav ul {\n  padding: 0;\n  margin: 0;\n  list-style: none;\n}\n  \n  #mobile-nav ul li {\n  position: relative;\n}\n  \n  #mobile-nav ul li a {\n  color: #fff;\n  font-size: 45px;\n  overflow: hidden;\n  padding: 10px 22px 10px 15px;\n  position: relative;\n  text-decoration: none;\n  width: 100%;\n  display: block;\n  outline: none;\n}\n  \n  #mobile-nav ul li a:hover {\n  color: #f82249;\n}\n  \n  #mobile-nav ul li li {\n  padding-left: 30px;\n}\n  \n  #mobile-nav ul .menu-has-children i {\n  position:static;\n  right: 0;\n  z-index: 300;\n  padding: 34px;\n  cursor: pointer;\n  color: #fff;\n}\n  \n  #mobile-nav ul .menu-has-children i.fa-chevron-up {\n  color: #f82249;\n}\n  \n  #mobile-nav ul .menu-item-active {\n  color: #f82249;\n}\n  \n  #mobile-body-overly {\n  width: 100%;\n  height: 100%;\n  z-index: 997;\n  top: 100;\n  left: 0;\n  position:relative;\n  background: rgba(6, 12, 34, 0.8);\n  display: none;\n}\n  \n  /* Mobile Nav body classes */\n  \n  body.mobile-nav-active {\n  overflow: hidden;\n}\n  \n  body.mobile-nav-active #mobile-nav {\n  left: 0;\n}\n  \n  body.mobile-nav-active #mobile-nav-toggle {\n  color: #fff;\n}\n  \n  header#header> .container {\n    display: flex;\n    justify-content: space-between;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"